* {
  margin: 0;
  padding: 0;
}

body {
  letter-spacing: .02em;
  color: #000;
  background-color: #fff;
  margin: auto;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 5vh 2vw;
}

.title {
  text-align: center;
  margin-bottom: 2vw;
}

.breed-select {
  margin: auto;
  display: block;
}

.cat-info {
  flex-direction: column;
  display: flex;
}

.cat-img {
  max-width: 100%;
  padding: 2vh 0;
}

.cat-img > img {
  width: 100%;
}

.cat-text {
  padding: 2vh 2vw;
}

.breed-name {
  margin-bottom: 12px;
  font-size: 32px;
  display: block;
}

.breed-description {
  margin-bottom: 24px;
  display: block;
}

.breed-temperament {
  display: block;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }

  .cat-text {
    padding: 2vh 0;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }

  .cat-info {
    flex-direction: row;
  }

  .cat-img, .cat-text {
    max-width: 50%;
    padding: 0 2vw;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }

  .cat-img {
    max-width: 30%;
  }

  .cat-text {
    max-width: 70%;
  }
}

